<template>
  <div>
    <v-app-bar app color="black" height="100">
      <div class="d-flex align-center ml-5">
        <router-link :to="{ name: 'Home' }">
          <v-row>
            <v-img
              alt="Logo"
              class="shrink mr-2"
              contain
              :src="require('@/assets/images/cmet_01.png')"
              transition="scale-transition"
              :max-width="'calc(40% - ' + $vuetify.application.top + 'px)'"
              :min-width="$vuetify.application.top - 50 + 'px'"
            />

            <v-img
              alt="LogoText"
              class="shrink"
              contain
              :src="require('@/assets/images/cmet_02.png')"
              transition="scale-transition"
              :max-width="'calc(40% - ' + $vuetify.application.top + 'px)'"
              :min-width="$vuetify.application.top - 30 + 'px'"
            />
          </v-row>
        </router-link>
      </div>

      <v-spacer></v-spacer>

      <div class="d-md-flex d-none">
        <v-btn depressed text class="mr-2" color="white" @click="toBuy()">
          Buy
        </v-btn>
        <v-btn
          depressed
          text
          class="mr-2"
          color="white"
          :to="{ name: 'Register' }"
          v-if="notLogin"
        >
          Sign Up
        </v-btn>
        <v-btn
          text
          dark
          depressed
          v-if="!notLogin"
          :to="{ path: '/member/accountVerify' }"
        >
          {{ userName }}
        </v-btn>
        <v-btn
          depressed
          text
          color="white"
          :to="{ name: 'Login' }"
          v-if="notLogin"
        >
          Log In
        </v-btn>
        <v-btn
          depressed
          text
          color="white"
          v-if="!notLogin"
          @click.prevent="logout"
        >
          Log Out
        </v-btn>
      </div>
      <div class="d-flex d-sm-none">
        <v-app-bar-nav-icon
          color="white"
          @click.stop="drawer = !drawer"
        ></v-app-bar-nav-icon>
      </div>
    </v-app-bar>

    <v-navigation-drawer v-model="drawer" app fixed temporary>
      <v-list v-if="notLogin">
        <v-list-item class="px-2">
          <v-list-item-avatar>
            <v-img
              :src="require('@/assets/images/head.png')"
              width="500"
            ></v-img>
          </v-list-item-avatar>
        </v-list-item>

        <v-list-item link>
          <v-list-item-content>
            <v-list-item-title class="title">未登入</v-list-item-title>
          </v-list-item-content>
        </v-list-item>
      </v-list>

      <v-list v-if="!notLogin">
        <v-list-item class="px-2">
          <v-list-item-avatar>
            <v-img src="https://cdn.vuetifyjs.com/images/john.jpg"></v-img>
          </v-list-item-avatar>
        </v-list-item>

        <v-list-item link>
          <v-list-item-content>
            <v-list-item-title class="title">{{ userName }}</v-list-item-title>
          </v-list-item-content>
        </v-list-item>
      </v-list>

      <v-divider></v-divider>

      <v-list nav dense>
        <v-list-item-group
          v-model="group"
          active-class="deep-purple--text text--accent-4"
        >
          <v-list-item :to="{ name: 'Login' }" v-if="notLogin">
            <v-list-item-title>Login</v-list-item-title>
          </v-list-item>

          <v-list-item :to="{ name: 'Register' }" v-if="notLogin">
            <v-list-item-title>Signup</v-list-item-title>
          </v-list-item>

          <v-list-item :to="{ path: '/member/accountVerify' }" v-if="!notLogin">
            <v-list-item-title>Member Area</v-list-item-title>
          </v-list-item>

          <v-list-item @click="logout" v-if="!notLogin">
            <v-list-item-title>logout</v-list-item-title>
          </v-list-item>
        </v-list-item-group>
      </v-list>
    </v-navigation-drawer>
  </div>
</template>

<script>
export default {
  data: () => ({
    drawer: false,
    group: null,
    notLogin: true,
    userName: "",
  }),

  watch: {
    group() {
      this.drawer = false;
    },
  },

  created() {
    this.$store.dispatch("getUserData");
  },

  mounted() {
    this.getAccount();
  },
  computed: {
    Email() {
      return localStorage.getItem("account");
    },
  },
  methods: {
    getAccount() {
      this.userName = this.Email ? this.Email : "未登入";
      this.notLogin = this.Email ? false : true;
    },

    logout() {
      this.$store.commit("$_removeStorage");
      localStorage.removeItem("bd_token");
      localStorage.removeItem("account");
      location.reload();
    },

    toBuy() {
      this.$router.push({ name: "PurchaseSteps" });
    },
  },
};
</script>