import Vue from 'vue'
import App from './App.vue'
import vuetify from './plugins/vuetify';
import router from './router'
import '@/assets/scss/style.scss'
import axios from "axios";
import VueAxios from "vue-axios"
import store from './store'
import Vuex from "vuex";
import SweetModal from 'sweet-modal-vue/src/plugin.js'
import VueMeta from 'vue-meta'

Vue.config.productionTip = false

// //axios config
// const auth_token = store.getters.getTokenSortage;

// const axiosConfig = {
//   headers: { 'Authorization': auth_token },
//   baseURL: "/api"
// };

// const axiosBase = axios.create(axiosConfig);
// Vue.prototype.$http = axiosBase;


router.beforeEach((to, from, next) => {
  //根據字段判斷路由是否過濾
  if (to.matched.some(record => record.meta.auth)) { //第一層帳密驗證
    const authToken = localStorage.getItem('auth_token')
    const bdToken = localStorage.getItem('bd_token')
    if (authToken && bdToken !== '' || authToken && bdToken !== null || authToken && bdToken !== "undefined") {
      next(); // 往下繼續執行
    } else {
      alert('Please login first')
      // 轉跳到 login page
      next('/login');
    }
  } else {
    next(); // 往下繼續執行
  }

  if (to.matched.some(record => record.meta.doubleAuth)) { //第二層實名驗證
    const authToken = localStorage.getItem('auth_token')
    const bdToken = localStorage.getItem('bd_token')

    if (authToken && bdToken !== '' || authToken && bdToken !== null || authToken && bdToken !== "undefined") {
      if (store.getters.getVerifiedEmail && store.getters.getVerifiedEmail == 2) {
        next(); // 往下繼續執行
      } else {
        alert('Please verify first')
        next('/member/accountVerify');
      }
    } else {
      alert('Please login first')
      // 轉跳到 login page
      next('/login');
    }
  } else {
    next(); // 往下繼續執行
  }
});

Vue.use(VueAxios, axios);
Vue.use(Vuex);
Vue.use(VueMeta)
Vue.use(SweetModal)

new Vue({
  vuetify,
  router,
  store,
  render: h => h(App)
}).$mount('#app')
