import Vue from 'vue'
import Vuex from 'vuex'
import router from '../router'
import { userInfo } from '../api'

Vue.use(Vuex)
Vue.use(router)


export default new Vuex.Store({
    state: {
        userName: "",
        email: "",
        phone: "",
        walletBalance: "",
        wallet: "",
        ETH_Amt: "",
        pause: "",
        verifiedEmail: localStorage.getItem('emailPass'),
        verifiedPhone: localStorage.getItem('phonePass'),
        token: localStorage.getItem('auth_token') ? localStorage.getItem('auth_token') : '',
        barDisplay: localStorage.getItem('account') ? localStorage.getItem('account') : ''
    },
    getters: {
        getTokenSortage: function (state) {
            if (!state.token) {
                state.token = localStorage.getItem('auth_token')
            }
            return state.token
        },

        getAccountSortage: function (state) {
            if (!state.barDisplay) {
                state.barDisplay = localStorage.getItem('account')
            }
            return state.barDisplay
        },
        getVerifiedEmail: function (state) {
            if (!state.verifiedEmail) {
                state.barDisplay = localStorage.getItem('emailPass')
            }
            return state.verifiedEmail
        },
        getVerifiedPhone: function (state) {
            if (!state.verifiedPhone) {
                state.barDisplay = localStorage.getItem('phonePass')
            }
            return state.verifiedPhone
        }
    },
    mutations: {
        USERNAME(state, value) {
            state.userName = value
        },
        EMAIL(state, value) {
            state.email = value
        },
        PHONE(state, value) {
            state.phone = value
        },
        WALLET(state, value) {
            state.wallet = value
        },
        ETH_AMT(state, value) {
            state.ETH_Amt = value
        },
        PAUSE(state, value) {
            state.pause = value
        },
        WALLETBALANCE(state, value) {
            state.walletBalance = value
        },
        VERIFIEDEMAIL(state, value) {
            state.verifiedEmail = value
            localStorage.setItem('emailPass', value)
        },
        VERIFIEDPHONE(state, value) {
            state.verifiedPhone = value
            localStorage.setItem('phonePass', value)
        },
        $_setTokenStorage(state, value) {
            state.token = value;
            localStorage.setItem('auth_token', value)
        },
        $_setAccountStorage(state, value) {
            state.barDisplay = value;
            localStorage.setItem('account', value)
        },
        $_removeStorage(state) {
            state.token = null;
            state.barDisplay = null;
            localStorage.removeItem('auth_token')
            localStorage.removeItem('account')
        }
    },
    actions: {
        getUserData({ commit }) {
            userInfo().then((result) => {
                if (result.data.success == true) {
                    console.log(result)
                    commit("USERNAME", result.data.Nickname)
                    commit('EMAIL', result.data.Email)
                    commit('PHONE', result.data.Mobile_Num)
                    commit('WALLET', result.data.Wallet)
                    commit('ETH_AMT', result.data.ETH_Amt)
                    commit('PAUSE', result.data.IsPause)
                    commit('WALLETBALANCE', result.data.Sum_Amt)
                    commit('VERIFIEDEMAIL', result.data.Is_Email)
                    commit('VERIFIEDPHONE', result.data.Is_Mobile)
                }
            })
                .catch((err) => {
                    console.error(err);
                });
        },
    },
})
