import Vue from "vue";
import VueRouter from "vue-router";
import Layout from "@/components/Layout";

Vue.use(VueRouter);

const routes = [
    {
        path: "/",
        component: Layout,
        children: [
            {
                path: "/",
                component: () => import('@/components/Home'),
                name: "Home",
                meta: {
                    title: '首頁',
                    auth: false
                }
            },
            {
                path: "/purchase",
                component: () => import('@/components/PurchaseSteps'),
                name: "PurchaseSteps",
                meta: {
                    title: '購買流程',
                    doubleAuth: true
                },
            },
            {
                path: "/member",
                component: () => import('@/components/Member'),
                name: "Member",
                meta: {
                    title: '會員中心',
                    auth: true
                },
                children: [
                    {
                        path: '/member/accountVerify',
                        component: () => import('@/components/MemberElement/AccountVerify'),
                        name: 'AccountVerify'
                    },
                    {
                        path: '/member/wallet',
                        component: () => import('@/components/MemberElement/Wallet'),
                        name: 'Wallet'
                    },
                    {
                        path: '/member/passwordChange',
                        component: () => import('@/components/MemberElement/PwdChange'),
                        name: 'PwdChange'
                    },
                    {
                        path: '/member/transactionRecord',
                        component: () => import('@/components/MemberElement/Record'),
                        name: 'Record'
                    },
                    {
                        path: '/member/myMiner',
                        component: () => import('@/components/MemberElement/MyMiner'),
                        name: 'Myminer'
                    }
                ]
            },
            {
                path: "/password_resets",
                component: () => import('@/components/ForgetPas'),
                name: "ForgetPassword",
                meta: {
                    title: '忘記密碼',
                    auth: false
                }
            },
            {
                path: "/deposit",
                component: () => import('@/components/Deposit'),
                name: "Deposit",
                meta: {
                    title: '儲值流程',
                    doubleAuth: true
                },
            },
        ]
    },
    {
        path: "/login",
        component: () => import('@/components/Login'),
        name: "Login",
        meta: {
            title: '登入',
            auth: false
        }
    },
    {
        path: "/register",
        component: () => import('@/components/Register'),
        name: "Register",
        meta: {
            title: '註冊',
            auth: false
        }
    },
];


const router = new VueRouter({
    mode: 'history',
    base: process.env.BASE_URL,
    routes,
});

export default router;